import * as React from "react";
import "components/canvas/canvas.css";
import { DropTarget, DropTargetMonitor } from "react-dnd";
import { DraggableTypes } from "types/draggable_types";
import ReactDOM = require("react-dom");
import { CanvasShape, ICanvasShapeProps } from "components/canvas/canvas_shape";
import { ShapeType } from "types/shape_type";
import { IShape } from "components/canvas/shape";

export const canvasShapeDefaultSize = 100;
interface ICanvasProps {
  connectDropTarget?: (content: JSX.Element) => JSX.Element;
}

interface ICanvasState {
  shapes: IShape[];
  next_shape_id: number;
  selectedShape?: number;
  current_max_z_index: number;
}

export class CanvasComponent extends React.Component<ICanvasProps, ICanvasState> {
  private offsetX!: number;
  private offsetY!: number;

  constructor(props: ICanvasProps) {
    super(props);

    this.state = { shapes: [], next_shape_id: 1, current_max_z_index: 1 };
    this.selectComponent = this.selectComponent.bind(this);
    this.unselectComponent = this.unselectComponent.bind(this);
  }

  public render(): JSX.Element {
    const { connectDropTarget } = this.props;

    if (connectDropTarget) {
      return connectDropTarget(this.renderContent());
    } else {
      return this.renderContent();
    }
  }

  public componentDidMount(): void {
    const drawingCanvas: Element = ReactDOM.findDOMNode(this) as Element;
    const clientRect: ClientRect = drawingCanvas.getBoundingClientRect();

    this.offsetX = clientRect.left;
    this.offsetY = clientRect.top;
  }

  public draw(type: ShapeType, drawCoordinate: IPoint): void {
    const shapeProps: IShape = {
      type,
      size: canvasShapeDefaultSize,
      topCoordinate: drawCoordinate,
      id: this.state.next_shape_id,
      z_index: this.state.current_max_z_index,
    };

    this.setState((prevState, props) => ({
      shapes: [...prevState.shapes, shapeProps],
      next_shape_id: prevState.next_shape_id + 1,
    }));
  }

  public moveShape(id: number, drawCoordinate: IPoint): void {
    const shapes = this.state.shapes;

    const shape = shapes.find(s => s.id === id);
    if (shape === undefined) {
      throw new Error("Unknown item has been dragged");
    }
    shape.topCoordinate = drawCoordinate;

    this.setState((prevState, props) => ({ shapes }));
  }

  public convertDragCoordToDrawCoord(dragCoordinate: IPoint): IPoint {
    return { x: dragCoordinate.x - this.offsetX, y: dragCoordinate.y - this.offsetY };
  }

  public coordinateIsInBoundsOf(coordinate: IPoint, size: number): boolean {
    const drawingCanvas: Element = ReactDOM.findDOMNode(this) as Element;

    return (
      coordinate.x >= 0 &&
      coordinate.y >= 0 &&
      coordinate.x + size <= drawingCanvas.clientWidth &&
      coordinate.y + size <= drawingCanvas.clientHeight
    );
  }

  private renderContent(): JSX.Element {
    const { shapes, selectedShape } = this.state;

    return (
      <div className="canvas" onClick={this.unselectComponent}>
        {shapes.map(shape => (
          <CanvasShape
            shape={shape}
            key={shape.id}
            onSelectionChange={this.selectComponent}
            isSelected={selectedShape === shape.id ? true : false}
          />
        ))}
      </div>
    );
  }

  private selectComponent(key: number): void {
    const shape = this.state.shapes.find(s => s.id === key);
    if (shape === undefined) {
      throw new Error("Unknown item has been dragged");
    }
    shape.z_index = this.state.current_max_z_index + 1;

    this.setState((prevState, props) => ({
      current_max_z_index: prevState.current_max_z_index + 1,
      selectedShape: key,
    }));
  }

  private unselectComponent(): void {
    this.setState((prevState, props) => ({ selectedShape: undefined }));
  }
}

const canvasTarget = {
  drop: (props: any, monitor: DropTargetMonitor, component: CanvasComponent) => {
    const dragCoordinate = monitor.getSourceClientOffset() as {
      x: number;
      y: number;
    };
    const drawCoordinate = component.convertDragCoordToDrawCoord(dragCoordinate);
    const size = monitor.getItem().size || canvasShapeDefaultSize;

    if (component.coordinateIsInBoundsOf(drawCoordinate, size)) {
      if (monitor.getItemType() === DraggableTypes.PALETTE_SHAPE) {
        component.draw(monitor.getItem().type, drawCoordinate);
      }
      if (monitor.getItemType() === DraggableTypes.CANVAS_SHAPE) {
        component.moveShape(monitor.getItem().id, drawCoordinate);
      }
    }
  },
};

export const Canvas = DropTarget(
  [DraggableTypes.PALETTE_SHAPE, DraggableTypes.CANVAS_SHAPE],
  canvasTarget,
  (connect: any, monitor: DropTargetMonitor) => {
    return { connectDropTarget: connect.dropTarget(), canDrop: monitor.canDrop() };
  }
)(CanvasComponent);
