import * as React from "react";
import { IShape } from "components/canvas/shape";
import { canvasShapeTypes } from "components/canvas/shapes/canvas_shape_types";
import { DraggableTypes } from "types/draggable_types";
import { DragSource } from "react-dnd";
import { ShapeType } from "types/shape_type";

export interface ICanvasShapeProps {
  shape: IShape;
  isSelected: boolean;
  onSelectionChange: (key: number) => void;
  isDragging?: boolean;
  connectDragSource?: (content: JSX.Element) => JSX.Element;
}

export class CanvasShapeComponent extends React.Component<ICanvasShapeProps> {
  constructor(props: ICanvasShapeProps) {
    super(props);

    this.handleClick = this.handleClick.bind(this);
  }

  public render(): JSX.Element {
    const { shape, isSelected, isDragging, connectDragSource } = this.props;
    if (!(shape.type in canvasShapeTypes)) {
      throw new TypeError("Shape type unsupported by the editor");
    }

    const content = this.renderContent(shape, isSelected);

    if (connectDragSource) {
      return connectDragSource(content);
    } else {
      return content;
    }
  }
  private renderContent(shape: IShape, isSelected: boolean): JSX.Element {
    const Shape = canvasShapeTypes[shape.type];
    const elementStyles = {
      transform: `translate(${shape.topCoordinate.x}px, ${shape.topCoordinate.y}px)`,
      zIndex: shape.z_index,
    };

    let shapeClassName = "canvas-shape";
    isSelected && (shapeClassName += " canvas-shape-selection");
    return (
      <div className={shapeClassName} style={elementStyles} onClick={this.handleClick} onMouseDown={this.handleClick}>
        <Shape topCoordinate={shape.topCoordinate} size={shape.size} />
      </div>
    );
  }

  private handleClick(e: React.MouseEvent<HTMLElement>): void {
    e.stopPropagation();
    this.props.onSelectionChange(this.props.shape.id);
  }
}

const shapeSource = {
  beginDrag: (props: ICanvasShapeProps) => {
    return { id: props.shape.id, size: props.shape.size };
  },
};

export const CanvasShape = DragSource(DraggableTypes.CANVAS_SHAPE, shapeSource, (connect, monitor) => {
  return {
    connectDragSource: connect.dragSource(),
    isDragging: monitor.isDragging(),
  };
})(CanvasShapeComponent);
