import * as React from "react";
import { Svg } from "controls/svg";

interface ITriangleProps {
  topCoordinate: IPoint;
  size: number;
}

export class Triangle extends React.Component<ITriangleProps> {
  public render(): JSX.Element {
    const { topCoordinate, size } = this.props;
    const points = `0, ${size} ${size}, ${size} ${size / 2}, 0`;

    return (
      <Svg width={size} height={size}>
        <polygon points={points} fill="black" stroke="white" />
      </Svg>
    );
  }
}
