import * as React from "react";
import { Svg } from "controls/svg";

interface ICircleProps {
  topCoordinate: IPoint;
  size: number;
}

export class Circle extends React.Component<ICircleProps> {
  public render(): JSX.Element {
    const { topCoordinate, size } = this.props;
    const radius = size / 2;

    return (
      <Svg width={size} height={size}>
        <circle
          cx={radius}
          cy={radius}
          r={radius}
          fill="black"
          stroke="white"
        />
      </Svg>
    );
  }
}
