import * as React from "react";
import { DragSource } from "react-dnd";
import { DraggableTypes } from "types/draggable_types";
import { ShapeType } from "types/shape_type";
import { Svg } from "controls/svg";

interface IPaletteShapeProps {
  type: ShapeType;
  isDragging?: boolean;
  connectDragSource?: (content: JSX.Element) => JSX.Element;
}

export class PaletteShapeComponent extends React.Component<IPaletteShapeProps> {
  public render(): JSX.Element {
    const { type, isDragging, connectDragSource } = this.props;
    const content = this.renderContent(type, isDragging);

    if (connectDragSource) {
      return connectDragSource(content);
    } else {
      return content;
    }
  }

  private renderContent(type: ShapeType, isDragging?: boolean): JSX.Element {
    return (
      <div className="my-shape" style={{ opacity: isDragging ? 0.5 : 1 }}>
        {this.renderShapeByType(type)}
      </div>
    );
  }

  private renderShapeByType(type: ShapeType): JSX.Element {
    switch (type) {
      case ShapeType.Rectangle:
        return (
          <Svg viewBox="0 0 100 100" width="100%" height="100%">
            <rect fill="black" style={{ width: "100%", height: "100%" }} />
          </Svg>
        );

      case ShapeType.Triangle:
        return (
          <Svg viewBox="0 0 100 100" width="100%" height="100%">
            <polygon points="100,100 0,100 50,0" fill="black" />
          </Svg>
        );

      case ShapeType.Circle:
        return (
          <Svg viewBox="0 0 100 100" width="100%" height="100%">
            <circle fill="black" cx="50" cy="50" r="50" />
          </Svg>
        );

      case ShapeType.LFCLogo:
        return (
          <img
            style={{ width: "100%", height: "100%" }}
            src="https://lh3.googleusercontent.com/QqBAv2diPTfMgmsK16kLvMduUkpTZyP6hdO95Bdjf17hiMqkTyLba3oPz8qgSt0tnVlUWlKt2ehA8OM8Lu42DQ4"
          />
        );
    }
  }
}

const shapeSource = {
  beginDrag: (props: IPaletteShapeProps) => {
    return { type: props.type };
  }
};

export const PaletteShape = DragSource(
  DraggableTypes.PALETTE_SHAPE,
  shapeSource,
  (connect, monitor) => {
    return {
      connectDragSource: connect.dragSource(),
      isDragging: monitor.isDragging()
    };
  }
)(PaletteShapeComponent);
