import * as React from "react";

interface ISvgProps {
  width: number | string;
  height: number | string;
  viewBox?: string;
}

export class Svg extends React.Component<ISvgProps> {
  public render(): JSX.Element {
    const { children, width, height, viewBox } = this.props;

    return (
      <svg
        width={width}
        height={height}
        viewBox={viewBox}
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
      >
        {children}
      </svg>
    );
  }
}
